import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { LoadingController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class VendorService {

  constructor(
    private http: Http,
    private _loadingController: LoadingController
  ) { }

  getVendors(id = 0) {
    let loader = this.getLoader();
    this.presentLoading(loader);
    return this.http.get(`https://cpsm.azurewebsites.net/public/index.php/get/vendor/${id}`)
      .pipe(
        map(response => {
          console.log(response);
          this.dismissLoading(loader);
          return response.json();
        }),
        catchError(error => {
          console.log(error);
          this.dismissLoading(loader);
          return throwError(error);
        })
      );
  }

  addVendor(vendor)  {
    let loader = this.getLoader();
    this.presentLoading(loader);
    return this.http.post(`https://cpsm.azurewebsites.net/public/index.php/post/vendor`, vendor)
      .pipe(
        map(response => { 
          console.log(response); 
          this.dismissLoading(loader);
          return response.json();
        }),
        catchError(error => {
          console.log(error);
          this.dismissLoading(loader);
          return throwError(error);
        })
      );
  }

  updateVendors(vendor) {
    let loader = this.getLoader();
    this.presentLoading(loader);
    return this.http.put(`https://cpsm.azurewebsites.net/public/index.php/put/vendor/${vendor.vendor_id}`, vendor)
      .pipe(
        map(response => { 
          console.log(response); 
          this.dismissLoading(loader);
          return response.json(); 
        }),
        catchError(error => {
          console.log(error);
          this.dismissLoading(loader);
          return throwError(error);
        })
      );
  }
  async getLoader() {
    return await this._loadingController.create({
      spinner: 'lines'
    });
  }

  presentLoading(loader) {
    loader.then(l => {
      l.present();
    })
  }

  dismissLoading(loader) {
    loader.then(l => {
      l.dismiss();
    })
  }
}
