import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ModalController, ToastController } from '@ionic/angular';
import { VendorService } from 'src/app/shared/services/vendor.service';

@Component({
  selector: 'app-edit-vendor',
  templateUrl: './edit-vendor.component.html',
  styleUrls: ['./edit-vendor.component.scss'],
})
export class EditVendorComponent implements OnInit {
  subscriptions: any = [];
  @Input() vendor;
  vendorForm: FormGroup;

  constructor(
    private _fb: FormBuilder,
    private _modalController: ModalController,
    private _vendorService: VendorService,
    private _toastController: ToastController
  ) { 
    this.vendorForm = _fb.group({
      vendor_id: new FormControl(0),
      name: new FormControl('', Validators.required),
      contact_no: new FormControl('', Validators.required),
      address: new FormControl('', Validators.required),
      rate: new FormControl('', Validators.required),
      mawa_katoti: new FormControl('', Validators.required),
      opening_balance: new FormControl('', Validators.required),
    })
  }

  ngOnInit() {
    console.log(this.vendor);
    if (this.vendor && this.vendor.vendor_id) this.getVendors(this.vendor.vendor_id);
  }
  
  getVendors(vendorId) {
    this.subscriptions.push(this._vendorService.getVendors(vendorId).subscribe(res => {
      console.log(res);
      this.vendor = res[0];
      this.vendorForm.patchValue(this.vendor);
    }))
  }

  onSave() {
    console.log(this.vendorForm.value);
    if (this.vendor) this.updateVendors(this.vendorForm.value)
    else this.addVendor(this.vendorForm.value);
  }

  updateVendors(vendor) {
    this.subscriptions.push(this._vendorService.updateVendors(vendor).subscribe(res => {
      if (res && res.success) {
        this.onSuccess("Updated");
      } else {
        this.onError("Error");
      }
      this.onClose();
    }, err => {
      this.onError("Error");
      this.onClose();
    }));
  }

  addVendor(vendor) {
    this.subscriptions.push(this._vendorService.addVendor(vendor).subscribe(res => {
      if (res && res.success) {
        this.onSuccess("Added");
      } else {
        this.onError("Error");
      }
      this.onClose();
    }, err => {
      this.onError("Error");
      this.onClose();
    }));
  }
  
  onCancel() {
    this.onClose();
  }

  onClose() {
    this._modalController.dismiss();
  }

  async onSuccess(message) {
    const toast = await this._toastController.create({
      message: message,
      duration: 2000,
      color: 'success',
    });
    toast.present();
  }

  async onError(message) {
    const toast = await this._toastController.create({
      message: message,
      duration: 2000,
      color: 'danger',
    });
    toast.present();
  }
  
}
